@mixin nav-link-style {
  font-family: Poppins, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 13px;
  line-height: 150%;
  letter-spacing: 3px;
  color: #fff;
  text-decoration: none;
  margin-bottom: 2px;
}

$side-margin-design: 14vw;
$side-margin-tab: 100px;

$tab-width: 1024px;

$headers-font: "Poppins", sans-serif;
$text-font: "Open Sans", sans-serif;

@mixin heading-style {
  font-family: $headers-font;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  color: #253757;
}

@mixin text-style {
  font-family: $text-font;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  color: #6c788b;
}

$process-block-dimension: 264px;

$main-grey-color-rgba: rgba(213, 213, 213, 0.2);
$main-dark-blue-color: #2060f6;

$main-dark-blue-color-rgba: rgba($color: #2060f6, $alpha: 0.2);
